import { NextResponse } from 'next/server';
import pool from '@/app/lib/db';

// PUT: ‡∏≠‡∏±‡∏õ‡πÄ‡∏î‡∏ï‡∏™‡∏ñ‡∏≤‡∏ô‡∏∞ -> ‡∏Ñ‡∏≥‡∏ô‡∏ß‡∏ì‡∏†‡∏≤‡∏©‡∏µ -> ‡∏™‡∏£‡πâ‡∏≤‡∏á‡πÉ‡∏ö‡πÄ‡∏™‡∏£‡πá‡∏à
export async function PUT(request, { params }) {
    // ‡∏£‡∏≠‡∏á‡∏£‡∏±‡∏ö Next.js 15+
    const { id } = await params;
    const body = await request.json(); // ‡∏£‡∏±‡∏ö‡∏Ñ‡πà‡∏≤ { status, wht_amount }

    console.log("Updating Invoice ID:", id, "Action:", body.status);

    if (body.status === 'paid') {
        const connection = await pool.getConnection();
        try {
            await connection.beginTransaction();

            // 1. ‚úÖ ‡∏≠‡∏±‡∏õ‡πÄ‡∏î‡∏ï‡∏™‡∏ñ‡∏≤‡∏ô‡∏∞‡πÉ‡∏ô‡∏ï‡∏≤‡∏£‡∏≤‡∏á invoices ‡πÄ‡∏õ‡πá‡∏ô 'paid'
            const [updateResult] = await connection.execute(
                'UPDATE invoices SET status = ? WHERE id = ?',
                ['paid', id]
            );

            if (updateResult.affectedRows === 0) {
                throw new Error(`Invoice ID ${id} not found`);
            }

            // 2. ‚úÖ ‡∏î‡∏∂‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏• Invoice ‡πÄ‡∏û‡∏∑‡πà‡∏≠‡∏ô‡∏≥‡πÑ‡∏õ‡∏Ñ‡∏≥‡∏ô‡∏ß‡∏ì‡∏¢‡∏≠‡∏î‡πÉ‡∏ô‡πÉ‡∏ö‡πÄ‡∏™‡∏£‡πá‡∏à
            const [rows] = await connection.execute('SELECT * FROM invoices WHERE id = ?', [id]);
            const invoice = rows[0];

            if (invoice) {
                const newDocNumber = invoice.doc_number.replace('INV', 'RC');

                // ‡∏Ñ‡∏≥‡∏ô‡∏ß‡∏ì‡∏¢‡∏≠‡∏î‡πÄ‡∏á‡∏¥‡∏ô (‡∏´‡∏•‡∏±‡∏Å‡∏ö‡∏±‡∏ç‡∏ä‡∏µ: ‡∏¢‡∏≠‡∏î‡πÄ‡∏ï‡πá‡∏° - ‡∏´‡∏±‡∏Å ‡∏ì ‡∏ó‡∏µ‡πà‡∏à‡πà‡∏≤‡∏¢ = ‡∏¢‡∏≠‡∏î‡∏£‡∏±‡∏ö‡∏à‡∏£‡∏¥‡∏á)
                const totalAmount = parseFloat(invoice.grand_total);
                const whtAmount = parseFloat(body.wht_amount || 0);
                const netAmount = totalAmount - whtAmount;

                // 3. ‚úÖ ‡∏ö‡∏±‡∏ô‡∏ó‡∏∂‡∏Å‡∏•‡∏á‡∏ï‡∏≤‡∏£‡∏≤‡∏á receipts (‡πÄ‡∏û‡∏¥‡πà‡∏°‡∏Ñ‡∏≠‡∏•‡∏±‡∏°‡∏ô‡πå wht_amount ‡πÅ‡∏•‡∏∞ net_amount)
                await connection.execute(
                    `INSERT INTO receipts 
                    (invoice_id, doc_number, doc_date, amount, wht_amount, net_amount, payment_method, created_at) 
                    VALUES (?, ?, NOW(), ?, ?, ?, 'Transfer', NOW())`,
                    [invoice.id, newDocNumber, totalAmount, whtAmount, netAmount]
                );

                console.log(`Receipt created: ${newDocNumber} | Net: ${netAmount}`);
            }

            await connection.commit();
            return NextResponse.json({ success: true, message: "‡∏ö‡∏±‡∏ô‡∏ó‡∏∂‡∏Å‡∏Å‡∏≤‡∏£‡∏ä‡∏≥‡∏£‡∏∞‡πÄ‡∏á‡∏¥‡∏ô‡πÄ‡∏£‡∏µ‡∏¢‡∏ö‡∏£‡πâ‡∏≠‡∏¢" });

        } catch (error) {
            await connection.rollback();
            console.error("Database Error:", error);
            return NextResponse.json({ error: error.message }, { status: 500 });
        } finally {
            connection.release();
        }
    }

    // ‡∏Å‡∏£‡∏ì‡∏µ‡∏™‡∏ñ‡∏≤‡∏ô‡∏∞‡∏≠‡∏∑‡πà‡∏ô ‡πÄ‡∏ä‡πà‡∏ô ‡∏¢‡∏Å‡πÄ‡∏•‡∏¥‡∏Å‡πÄ‡∏≠‡∏Å‡∏™‡∏≤‡∏£
    if (body.status === 'cancelled') {
        const connection = await pool.getConnection();
        try {
            await connection.execute('UPDATE invoices SET status = ? WHERE id = ?', ['cancelled', id]);
            return NextResponse.json({ success: true, message: "‡∏¢‡∏Å‡πÄ‡∏•‡∏¥‡∏Å‡πÄ‡∏≠‡∏Å‡∏™‡∏≤‡∏£‡πÄ‡∏£‡∏µ‡∏¢‡∏ö‡∏£‡πâ‡∏≠‡∏¢" });
        } catch (error) {
            return NextResponse.json({ error: error.message }, { status: 500 });
        } finally {
            connection.release();
        }
    }

    return NextResponse.json({ error: "Invalid Action" }, { status: 400 });
}

// GET: ‡∏î‡∏∂‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏£‡∏≤‡∏¢‡∏•‡∏∞‡πÄ‡∏≠‡∏µ‡∏¢‡∏î‡πÄ‡∏≠‡∏Å‡∏™‡∏≤‡∏£
export async function GET(request, { params }) {
    const { id } = await params; // ‡∏£‡∏≠‡∏á‡∏£‡∏±‡∏ö Next.js 15+

    try {
        // ‚úÖ SQL Join ‡πÄ‡∏û‡∏∑‡πà‡∏≠‡∏î‡∏∂‡∏á‡∏ä‡∏∑‡πà‡∏≠‡πÇ‡∏Ñ‡∏£‡∏á‡∏Å‡∏≤‡∏£ p.project_name ‡πÅ‡∏•‡∏∞‡∏Ñ‡πà‡∏≤ i.quantity
        const sql = `
            SELECT 
                i.*, 
                p.project_name 
            FROM invoices i
            LEFT JOIN projects p ON i.project_id = p.id
            WHERE i.id = ?
        `;

        const [rows] = await pool.query(sql, [id]);

        if (rows.length === 0) {
            return NextResponse.json({ error: "‡πÑ‡∏°‡πà‡∏û‡∏ö‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÉ‡∏ö‡πÅ‡∏à‡πâ‡∏á‡∏´‡∏ô‡∏µ‡πâ" }, { status: 404 });
        }

        const inv = rows[0];

        // ‚úÖ ‡∏Ñ‡∏≥‡∏ô‡∏ß‡∏ì‡∏´‡∏≤‡∏Ñ‡πà‡∏≤‡∏ï‡πà‡∏≠‡∏´‡∏ô‡πà‡∏ß‡∏¢‡∏à‡∏£‡∏¥‡∏á (‡∏¢‡∏≠‡∏î‡∏£‡∏ß‡∏°‡∏´‡∏≤‡∏£‡∏î‡πâ‡∏ß‡∏¢‡∏à‡∏≥‡∏ô‡∏ß‡∏ô)
        const items = [
            {
                description: inv.project_name ? `‡πÇ‡∏Ñ‡∏£‡∏á‡∏Å‡∏≤‡∏£ : ${inv.project_name}` : '‡∏Ñ‡πà‡∏≤‡∏ö‡∏£‡∏¥‡∏Å‡∏≤‡∏£/‡∏™‡∏¥‡∏ô‡∏Ñ‡πâ‡∏≤‡∏ó‡∏±‡πà‡∏ß‡πÑ‡∏õ',
                quantity: inv.quantity || 1,
                // ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç‡∏ö‡∏£‡∏£‡∏ó‡∏±‡∏î‡∏•‡πà‡∏≤‡∏á‡∏ô‡∏µ‡πâ: ‡πÄ‡∏û‡∏∑‡πà‡∏≠‡πÉ‡∏´‡πâ‡∏£‡∏≤‡∏Ñ‡∏≤‡∏ï‡πà‡∏≠‡∏´‡∏ô‡πà‡∏ß‡∏¢‡∏ñ‡∏π‡∏Å‡∏ï‡πâ‡∏≠‡∏á
                unit_price: (parseFloat(inv.subtotal) / (inv.quantity || 1)),
                total: inv.subtotal
            }
        ];

        return NextResponse.json({ invoice: inv, items });

    } catch (error) {
        console.error("Database Error:", error);
        return NextResponse.json({ error: error.message }, { status: 500 });
    }
}